<template>
  <div>
    <NavTemplent title="角色管理"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <el-row class="tableTitle">
        <el-col :span="12">
          <h5>角色列表</h5>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <el-button size="mini" type="primary" @click="editUseMessage"><i class="el-icon-plus"></i>添加</el-button>
        </el-col>
      </el-row>
      <el-table
        :data="roleList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="角色">
          <template slot-scope="scope">
            <span>{{scope.row.role_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="部门">
          <template slot-scope="scope">
            <span>{{scope.row.name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="状态">
          <template slot-scope="scope">
            <el-button type="success" size="mini" @click="updateStatus(scope.row)" v-if="scope.row.status === 0">启用</el-button>
            <el-button type="danger" size="mini" @click="updateStatus(scope.row)" v-else>禁用</el-button>
          </template>
        </el-table-column>
        <el-table-column
          label="创建时间">
          <template slot-scope="scope">
            <span>{{scope.row.update_time | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="editUseMessage(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="角色名称">
            <el-input v-model="form.role_name" placeholder="请输入角色名称"></el-input>
          </el-form-item>
          <el-form-item label="角色描述">
            <el-input type="textarea" maxlength="100" v-model="form.role_desc" placeholder="请输入角色描述"></el-input>
          </el-form-item>
          <el-form-item>
            <label>角色权限</label>
            <el-tree
              ref="tree"
              :data="menuList"
              show-checkbox
              node-key="menu_id"
              :default-checked-keys="checkMenu"
              @check-change="handleCheckChange"
              :props="props">
            </el-tree>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                props: {
                    label: 'menu_name',
                    children: 'children'
                },
                menuIds: [],
                checkMenu: [],
                count: 1,
                editUse: null,
                title: '角色信息',
                dialogVisible: false,
                roleList: [],
                menuList: [],
                updateRole: null,
                form: {
                    role_name: '',
                    role_desc: ''
                }
            }
        },
        methods: {
            getroleList() {
                this.$post('/getRoleList').then(res => {
                    this.roleList = res.list;
                })
            },
            getmenuList() {
                this.$post('/getMenuList').then(res => {
                    this.menuList = res.filter(item => {
                        return item.status === 1
                    });
                })
            },
            handleCheckChange() {
                this.menuIds = JSON.stringify(this.$refs.tree.getCheckedKeys());
            },
            editUseMessage(data) {
                this.updateRole = data;
                this.dialogVisible = true;
                if (data.role_id) {
                    this.form = data;
                    this.checkMenu = JSON.parse(data.menu_list);
                } else {
                    this.form.role_name = '';
                    this.form.role_desc = '';
                    this.checkMenu = [];
                }
            },
            updateStatus(data) {
                this.$post('editRoleStatus', {
                    role_id: data.role_id
                }).then(res => {
                    this.getroleList();
                    this.$message.success('状态修改成功')
                })
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if(this.form.role_name === '') {
                    this.$message.error('角色名称不能为空');
                    return;
                }
                if(!this.menuIds.length) {
                    this.$message.error('角色权限不能为空');
                    return;
                }
                if (this.updateRole.role_id) {
                    this.$post('editRole', {
                        role_id: this.updateRole.role_id,
                        role_name: this.form.role_name,
                        menu_list: this.menuIds,
                        role_desc: this.form.role_desc
                    }).then(() => {
                        this.$message.success('角色新增成功');
                        this.getroleList();
                        this.dialogVisible = false;
                        this.$emit('close')
                    });
                } else {
                    this.$post('addRole', {
                        role_name: this.form.role_name,
                        menu_list: this.menuIds,
                        role_desc: this.form.role_desc
                    }).then(() => {
                        this.$message.success('角色新增成功');
                        this.getroleList();
                        this.dialogVisible = false;
                        this.$emit('close')
                    });
                }
            }
        },
        mounted() {
            this.getroleList();
            this.getmenuList()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
</style>



