<template>
  <div>
    <NavTemplent title="部门管理"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <el-row class="tableTitle">
        <el-col :span="12">
          <h5>部门列表</h5>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <el-button size="mini" type="primary" @click="editDepartment"><i class="el-icon-plus"></i>添加</el-button>
        </el-col>
      </el-row>
      <el-table
        :data="departmentList"
        style="width: 100%"
        row-key="department_id"
        border
        lazy
        :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="部门名称">
          <template slot-scope="scope">
            <span>{{scope.row.department_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="部门负责人">
          <template slot-scope="scope">
            <span>{{scope.row.department_manager || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="部门人数">
          <template slot-scope="scope">
            <span>{{scope.row.department_num || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="editDepartment(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="部门名称">
            <el-input v-model="form.department_name" placeholder="请输入部门名称"></el-input>
          </el-form-item>
          <el-form-item label="部门描述">
            <el-input type="textarea" maxlength="100" show-word-limit v-model="form.desc" placeholder="请输入部门描述"></el-input>
          </el-form-item>
          <el-form-item label="部门负责人">
            <el-select v-model="form.department_manager" placeholder="请选择部门负责人">
              <el-option v-for="item in userList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="上级部门">
            <el-select v-model="form.pid" placeholder="请选择上级部门">
              <el-option label="新增部门" :value="0"></el-option>
              <el-option v-for="item in departmentNameList" :key="item.pid" :label="item.department_name" :value="item.pid"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                isDepartment: false,
                editUse: null,
                title: '部门信息',
                dialogVisible: false,
                departmentMessage: null,
                userList: [],
                departmentNameList: [],
                departmentList: [],
                total: 0,
                form: {
                    department_name: '',
                    desc: '',
                    department_manager: '',
                    pid: ''
                }
            }
        },
        methods: {
            editDepartment(data) {
                this.isDepartment = true;
                this.dialogVisible = true;
                this.departmentMessage = data;
                if (data.department_id) {
                    this.form.department_manager = data.department_manager;
                    this.form.desc = data.desc;
                    this.form.department_name = data.department_name;
                    this.form.pid = data.pid
                } else {
                    this.form.department_manager = '';
                    this.form.desc = '';
                    this.form.department_name = '';
                    this.form.pid = ''
                }
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.form.department_name === '') {
                    this.$message.error('部门名称不能为空');
                    return;
                }
                if (this.form.department_manager === '') {
                    this.$message.error('部门负责人不能为空');
                    return;
                }
                if(this.form.pid === '') {
                    this.$message.error('上级部门不能为空');
                    return;
                }
                if (this.departmentMessage.department_id) {
                    this.form.department_id = this.departmentMessage.department_id;
                    this.$post('editDepartment',
                      this.form
                    ).then(res => {
                        this.$message.success('部门修改成功')
                    })
                } else {
                    this.$post('addDepartment', this.form).then(res => {
                        this.getDepartmentList();
                        this.dialogVisible = false;
                        this.$emit('close')
                    });
                }
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.total = res.length;
                    this.departmentList = res.list;
                    this.departmentNameList = res.list.map(item => {
                        return {
                            department_name: item.department_name,
                            pid: item.department_id
                        }
                    });
                })
            },
            getUserList() {
                this.$post('/getUserList').then(res => {
                    this.total = res.count;
                    this.userList = res.list
                })
            }
        },
        mounted() {
            this.getUserList();
            this.getDepartmentList()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
</style>



