<template>
  <div>
    <NavTemplent title="站点管理"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-form-item label="站点">
            <el-input v-model="form.siteName" placeholder="请输入站点名称" @change="getSiteList"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row class="tableTitle">
        <el-col :span="12">
          <h5>站点列表</h5>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <el-button size="mini" type="primary" @click="editUseMessage"><i class="el-icon-plus"></i>添加</el-button>
        </el-col>
      </el-row>
      <el-table
        :data="siteList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="站点名称">
          <template slot-scope="scope">
            <span>{{scope.row.website_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="站点类型">
          <template slot-scope="scope">
            <span>{{scope.row.website_type || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="创建时间">
          <template slot-scope="scope">
            <span>{{scope.row.update_time | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="editUseMessage(scope.row)">编辑</el-button>
            <el-button type="danger" size="small" @click="delUseMessage(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="siteList.length"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="站点名称">
            <el-input v-model="form.website_name" placeholder="请输入站点名称"></el-input>
          </el-form-item>
          <el-form-item label="站点API">
            <el-input v-model="form.website_api" placeholder="请输入站点API"></el-input>
          </el-form-item>
          <el-form-item label="站点类型">
            <el-select v-model="form.website_type" placeholder="请选择站点类型">
              <el-option label="shopify" :value="1"></el-option>
              <el-option label="独立站" :value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                editUse: null,
                title: '站点信息',
                dialogVisible: false,
                siteList: [],
                siteMessage: null,
                form: {
                    website_name: '',
                    website_api: '',
                    siteName: '',
                    website_type: ''
                }
            }
        },
        methods: {
            getSiteList() {
                this.$post('/getWebsiteList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            editUseMessage(data) {
                this.dialogVisible = true;
                this.siteMessage = data;
                if (data.website_id) {
                    this.form = data;
                } else {
                    this.form.website_name = '';
                    this.form.website_api = '';
                    this.form.website_type = '';
                }
            },
            delUseMessage(data) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$post('delWebsite', {
                        website_id: data.website_id
                    }).then(() => {
                        this.getSiteList();
                        this.$message.success('站点删除成功')
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.form.website_name === '') {
                    this.$message.error('站点名称不能为空');
                    return;
                }
                if(this.form.website_api === '') {
                    this.$message.error('站点API不能为空');
                    return;
                }
                if(this.form.website_type === '') {
                    this.$message.error('站点类型不能为空');
                    return;
                }
                if (this.siteMessage.website_id) {
                    this.$post('editWebsite', {
                        website_id: this.siteMessage.website_id,
                        website_name: this.form.website_name,
                        website_api: this.form.website_api,
                        website_type: this.form.website_type
                    }).then(() => {
                        this.$message.success('站点添加成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getSiteList();
                    });
                } else {
                    this.$post('addWebsite', {
                        website_name: this.form.website_name,
                        website_api: this.form.website_api,
                        website_type: this.form.website_type
                    }).then(() => {
                        this.$message.success('站点添加成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getSiteList();
                    });
                }
            }
        },
        mounted() {
            this.getSiteList()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
</style>



