<template>
  <div class="loginBox">
      <div class="login">
        <el-row>
          <el-col :span="12">
            <img src="../../../../static/img/login.png" alt="" style="max-width: 100%">
          </el-col>
          <el-col :span="12" style="padding-top: 30px">
            <img src="../../../../static/img/logo.png" alt="" style="margin-bottom: 15px">
            <h2 style="color: #409EFF;font-weight: bold">图量科技企业数据魔方系统</h2>
            <h3 style="color: #409EFF">Enterprise Data Service Solutions</h3>
            <el-form ref="form" style="margin-top: 50px">
              <el-form-item>
                <el-input v-model="form.name" prefix-icon="el-icon-s-custom" placeholder="请输入账号"></el-input>
              </el-form-item>
              <el-form-item>
                <el-input type="password" v-model="form.password" prefix-icon="el-icon-lock" placeholder="请输入密码"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="login">登录</el-button>
              </el-form-item>
              <el-form-item>
                <el-button @click="register">注册</el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                showNav: false,
                form: {
                    name: '',
                    password: ''
                }
            }
        },
        methods: {
            login(){
                if (this.form.name === '') {
                    this.$message.error('用户名不能为空');
                    return
                }
                if (this.form.password === '') {
                    this.$message.error('密码不能为空');
                    return
                }
                this.$post('/login', {
                    mobile: this.form.name,
                    password: this.form.password,
                    remember_me: '1'
                }).then(res => {
                    this.$message.success('登陆成功');
                    localStorage.setItem('token', res.access_token);
                    if (res.access_token) {
                        this.$router.push({
                            path: '/Common'
                        })
                    }
                });
            },
            register() {
                this.$router.push({
                    path: '/Register'
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
  .loginBox{
    text-align: center;
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    background-color: #25a4bb;
    position: relative;
    background-image: url("../../../../static/img/bjImg.png");
    .login{
      width: 800px;
      margin: 0 auto;
      background-color: #fff;
      position: absolute;
      left:50%;
      top:50%;
      transform: translate(-50%, -50%);
      .el-input, .el-button{
        width: 80%;
      }
    }
  }
</style>



