<template>
  <div style="padding: 15px;border: 1px solid #e0dfdf;margin: 15px;border-radius: 5px">
    <h3 style="font-weight: bolder">当天的数据</h3>
    <el-row>
      <el-col :span="12" style="padding: 10px;">
        <div style="border: 1px solid #ccc;padding: 5px;border-radius: 5px;">
          <div id="myChart" style="width: 100%;height: 400px"></div>
        </div>
      </el-col>
      <el-col :span="12" style="padding: 10px;">
        <div style="border: 1px solid #ccc;padding: 5px;border-radius: 5px;">
          <div id="myChart1" style="width: 100%;height: 400px"></div>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12" style="padding: 10px;border-radius: 5px;">
        <div style="border: 1px solid #ccc;padding: 5px;border-radius: 5px;">
          <div id="myChart2" style="width: 100%;height: 400px"></div>
        </div>
      </el-col>
      <el-col :span="12" style="padding: 10px;border-radius: 5px;">
        <div style="border: 1px solid #ccc;padding: 5px;border-radius: 5px;">
          <div style="width: 100%;height: 400px">
            <p style="font-weight: bold;font-size: 18px;color: #000;margin: 0;padding-left: 5px">转化率 0%</p>
              <el-row style="padding: 5px">
                <el-col :span="24" style="text-align: left">
                  <span>进入网站</span><br>
                  <span style="font-size: 14px;color: #23c323">0回话</span>
                </el-col>
              </el-row>
              <el-row style="padding: 5px">
                <el-col :span="12" style="text-align: left">
                  <span>加入购物车</span><br>
                  <span style="font-size: 14px;color: #23c323">0回话</span>
                </el-col>
                <el-col :span="12" style="text-align: right">0%</el-col>
              </el-row>
              <el-row style="padding: 5px">
                <el-col :span="12" style="text-align: left">
                  <span>提交订单</span><br>
                  <span style="font-size: 14px;color: #23c323">0回话</span>
                </el-col>
                <el-col :span="12" style="text-align: right">0%</el-col>
              </el-row>
              <el-row style="padding: 5px">
                <el-col :span="12" style="text-align: left">
                  <span>完成订单</span><br>
                  <span style="font-size: 14px;color: #23c323">0回话</span>
                </el-col>
                <el-col :span="12" style="text-align: right">0%</el-col>
              </el-row>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12" style="padding: 10px;border-radius: 5px;">
        <div style="border: 1px solid #ccc;padding: 5px;border-radius: 5px;">
          <div style="width: 100%;height: 400px">
            <p style="font-weight: bold;font-size: 18px;color: #000;margin: 0;padding-left: 5px">销量排行</p>
            <div style="text-align: center;line-height: 200px">暂无数据</div>
          </div>
        </div>
      </el-col>
      <el-col :span="12" style="padding: 10px;border-radius: 5px;">
        <div style="border: 1px solid #ccc;padding: 5px;border-radius: 5px;">
          <div id="myChart3" style="width: 100%;height: 400px"></div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
    export default {
        data() {
            return {

            }
        },
        methods: {
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '总销售额 $366'},
                    xAxis: {
                        type: 'category',
                        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [820, 932, 901, 934, 1290, 1330, 1320],
                        type: 'line'
                    }]
                });
            },
            drawLine1() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart1'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '总订单数 34单'},
                    xAxis: {
                        type: 'category',
                        data: ['00:00', '02:00', '04:00', '06:00', '08:00', '10:00', '12:00', '14:00', '16:00', '18:00', '20:00', '22:00', '24:00']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [820, 221, 654, 865, 543, 564, 1330, 984, 566, 1330, 984, 566],
                        type: 'line'
                    }]
                });
            },
            drawLine2() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart2'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '流量 0回话 0访客'},
                    xAxis: {
                        type: 'category',
                        data: ['00:00', '02:00', '04:00', '06:00', '08:00', '10:00', '12:00', '14:00', '16:00', '18:00', '20:00', '22:00', '24:00']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [820, 221, 654, 865, 543, 564, 1330, 984, 566, 1330, 984, 566],
                        type: 'line'
                    }]
                });
            },
            drawLine3() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart3'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '流量来源'},
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'left',
                        data:['搜索引擎','社交平台','直接输入','其他']
                    },
                    series: [
                        {
                            name:'访问来源',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:'搜索引擎'},
                                {value:335, name:'社交平台'},
                                {value:310, name:'直接输入'},
                                {value:234, name:'其他'}
                            ]
                        }
                    ]
                });
            },
        },
        mounted() {
            this.drawLine();
            this.drawLine1();
            this.drawLine2();
            this.drawLine3();
        }
    }
</script>

<style lang="scss" scoped>

</style>



