<template>
  <div>
    <NavTemplent title="推广费用"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="部门">
            <el-select v-model="form.departmentId" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="站点">
            <el-select v-model="form.site" placeholder="请选择站点">
              <el-option v-for="item in siteList" :key="item.website_id" :value="item.website_id" :label="item.website_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="渠道">
            <el-select v-model="form.channel" placeholder="请选择渠道">
              <el-option label="FaceBook" :value="0"></el-option>
              <el-option label="google" :value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row style="border: 1px solid #e6e3e3">
        <el-col :span="12" style="padding: 0 15px">
          <h5 style="text-align: center;font-weight: bolder">站点费用统计：lastchic</h5>
          <div id="myChart3" style="width: 100%;height: 300px"></div>
        </el-col>
        <el-col :span="12" style="padding: 0 15px">
          <h5 style="text-align: center;font-weight: bolder">
            ROI排行榜 本月
          </h5>
          <div class="tab">
            <span class="active">个人</span>
            <span>部门</span>
          </div>
          <el-table
            :data="promotionList"
            border
            style="width: 100%;height: 250px">
            <el-table-column
              type="index">
            </el-table-column>
            <el-table-column
              label="排名">
              <template slot-scope="scope">
                <span>{{scope.row.name || '--'}}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="姓名">
              <template slot-scope="scope">
                <span>{{scope.row.department_name || '--'}}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="销售额">
              <template slot-scope="scope">
                <span>{{scope.row.role_name || '--'}}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="ROI">
              <template slot-scope="scope">
                <span>{{scope.row.status}}</span>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row class="tableTitle">
        <el-col :span="12">
          <h5>费用列表</h5>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <el-button size="mini" @click="editUseMessage"><i class="el-icon-download"></i>导出</el-button>
        </el-col>
      </el-row>
      <el-table
        :data="promotionList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="姓名">
          <template slot-scope="scope">
            <span>{{scope.row.name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="广告费用">
          <template slot-scope="scope">
            <span>{{scope.row.department_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="销售额">
          <template slot-scope="scope">
            <span>{{scope.row.role_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="ROI">
          <template slot-scope="scope">
            <span>{{scope.row.status}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="渠道">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="个人客单价">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="添加时间">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="editUseMessage(scope.row)">编辑</el-button>
            <el-button type="danger" size="mini" @click="delUseMessage(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="姓名">
            <el-input v-model="form.name" placeholder="请输入名称"></el-input>
          </el-form-item>
          <el-form-item label="广告费用">
            <el-input v-model="form.cost" placeholder="请输入广告费用"></el-input>
          </el-form-item>
          <el-form-item label="销售额">
            <el-input v-model="form.password" placeholder="请输入销售额"></el-input>
          </el-form-item>
          <el-form-item label="ROI">
            <el-input v-model="form.email" placeholder="请输入ROI"></el-input>
          </el-form-item>
          <el-form-item label="渠道">
            <el-select v-model="form.department_id" placeholder="请选择渠道">
              <el-option label="FaceBook" :value="0"></el-option>
              <el-option label="google" :value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="分析原因">
            <el-input type="textarea"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                siteList: null,
                dialogVisible: false,
                promotionList: [],
                total: 0,
                title: '编辑费用',
                form: {
                    site: '',
                    date: [],
                    departmentId: '',
                    password: '',
                    cost: '',
                    userName: '',
                    department_id: '',
                    email: '',
                    name: '',
                    role_id: [],
                    status: 0
                }
            }
        },
        methods: {
            getSiteList() {
                this.$post('/getWebsiteList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            drawLine3() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart3'));
                // 绘制图表
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data:['搜索引擎','社交平台','直接输入','其他']
                    },
                    series: [
                        {
                            name:'访问来源',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:'搜索引擎'},
                                {value:335, name:'社交平台'},
                                {value:310, name:'直接输入'},
                                {value:234, name:'其他'}
                            ]
                        }
                    ]
                });
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            editUseMessage(data) {
                this.dialogVisible = true;
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {}
        },
        mounted() {
            this.getSiteList();
            this.getDepartmentList();
            this.drawLine3()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



