<template>
  <div>
    <NavTemplent title="物流费用"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="站点">
            <el-select v-model="form.site" placeholder="请选择站点">
              <el-option v-for="item in siteList" :key="item.website_id" :value="item.website_id" :label="item.website_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="状态">
            <el-select v-model="form.status" placeholder="请选择状态">
              <el-option label="审核通过" :value="0"></el-option>
              <el-option label="待通过" :value="1"></el-option>
              <el-option label="审核未通过" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row>
          <el-col :span="24">
            <div id="myChart" style="width: 100%;height: 400px"></div>
          </el-col>
      </el-row>
      <el-row class="tableTitle">
        <el-col :span="12">
          <h5>物流列表</h5>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <el-button size="mini" @click="editUseMessage"><i class="el-icon-download"></i>导出</el-button>
        </el-col>
      </el-row>
      <el-table
        :data="promotionList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="仓库单号">
          <template slot-scope="scope">
            <span>{{scope.row.name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="物流单号">
          <template slot-scope="scope">
            <span>{{scope.row.department_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="重量">
          <template slot-scope="scope">
            <span>{{scope.row.role_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="运输渠道">
          <template slot-scope="scope">
            <span>{{scope.row.status}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="单价">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="处理费">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="发货日期">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="总费用">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="editUseMessage(scope.row)">编辑</el-button>
            <el-button type="danger" size="mini" @click="delUseMessage(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="订单号">
            <el-input v-model="form.name" placeholder="请输入订单号"></el-input>
          </el-form-item>
          <el-form-item label="物流单号">
            <el-input v-model="form.mobile" placeholder="请输入物流单号"></el-input>
          </el-form-item>
          <el-form-item label="网站">
            <el-select v-model="form.password" placeholder="请选择网站">
              <el-option label="FaceBook" value="0"></el-option>
              <el-option label="Google" value="1"></el-option>
              <el-option label="FaceBook" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="运输渠道">
            <el-select v-model="form.email" placeholder="请选择渠道">
              <el-option label="FaceBook" value="0"></el-option>
              <el-option label="Google" value="1"></el-option>
              <el-option label="FaceBook" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="重量">
            <el-input v-model="form.password" placeholder="请输入重量"></el-input>
          </el-form-item>
          <el-form-item label="单价">
            <el-input v-model="form.email" placeholder="请输入单价"></el-input>
          </el-form-item>
          <el-form-item label="处理费">
            <el-input v-model="form.email" placeholder="请输入处理费"></el-input>
          </el-form-item>
          <el-form-item label="发货时间">
            <el-date-picker
              class="date"
              v-model="date"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="总费用">
            <el-input v-model="form.email" placeholder="请输入总费用"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                title: '编辑物流费用',
                date: '',
                editUserMessage: null,
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                siteList: null,
                editUse: null,
                dialogVisible: false,
                promotionList: [],
                roleList: [],
                total: 0,
                menu: false,
                uditUser: false,
                form: {
                    date: [],
                    departmentId: '',
                    password: '',
                    mobile: '',
                    userName: '',
                    department_id: '',
                    email: '',
                    name: '',
                    role_id: [],
                    status: 0
                }
            }
        },
        methods: {
            getSiteList() {
                this.$post('/getWebsiteList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '物流费用'},
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [820, 932, 901, 934, 1290, 1330, 1320],
                        type: 'line',
                        areaStyle: {}
                    }]
                });
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            getRoleList() {
                this.$post('getRoleList').then(res => {
                    this.roleList = res.list.filter(item => {
                        return item.status === 1
                    })
                })
            },
            editUseMessage(data) {
                this.editUserMessage = data;
                this.dialogVisible = true;
                if (data.id) {
                    this.form = data;
                    this.menu = true;
                    this.uditUser = false;
                }else {
                    this.form.mobile = '';
                    this.form.email = '';
                    this.form.name = '';
                    this.form.department_id = '';
                    this.form.role_id = [];
                    this.menu = false;
                    this.uditUser = false;
                }
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.editUserMessage.id) {
                    if (this.form.mobile === this.editUserMessage.mobile) {
                        this.mobile = null
                    } else {
                        this.mobile = this.form.mobile
                    }
                    if (this.uditUser) {
                        this.$post('editUser', {
                            email: this.editUserMessage.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.editUserMessage.name,
                            mobile: null,
                            department_id: this.form.department_id,
                            password: this.editUserMessage.password,
                            role_list: JSON.stringify(this.form.role_id)
                        }).then(res => {
                            this.$post('editUserStatus', {
                                user_id: this.editUserMessage.id
                            }).then(res => {
                                this.$message.success('状态修改成功');
                                this.dialogVisible = false;
                                this.$emit('close');
                            });
                        })
                    } else {
                        this.$post('editUser', {
                            email: this.form.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.form.name,
                            mobile: this.mobile,
                            department_id: this.form.department_id,
                            password: this.form.password,
                        }).then(res => {
                            this.$message.success('用户修改成功');
                            this.dialogVisible = false;
                            this.$emit('close');
                        })
                    }

                }else {
                    console.log(this.form);
                    this.$post('signUp', this.form).then(res => {
                        this.$message.success('用户新增成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                    })
                }
            }
        },
        mounted() {
            this.getDepartmentList();
            this.getRoleList();
            this.drawLine();
            this.getSiteList()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select, .date{
    width: 100%;
  }
</style>



