<template>
  <div>
    <NavTemplent title="菜单管理"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <el-row class="tableTitle">
        <el-col :span="12">
          <h5>菜单列表</h5>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <el-button size="mini" type="primary" @click="editUseMessage"><i class="el-icon-plus"></i>添加</el-button>
        </el-col>
      </el-row>
      <el-table
        style="width: 100%"
        row-key="menu_id"
        border
        lazy
        :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
        :data="menuList">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="菜单名称">
          <template slot-scope="scope">
            <span>{{scope.row.menu_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="菜单状态">
          <template slot-scope="scope">
            <el-button type="success" size="mini" @click="updateStatus(scope.row)" v-if="scope.row.status === 0">启用</el-button>
            <el-button type="danger" size="mini" @click="updateStatus(scope.row)" v-else>禁用</el-button>
          </template>
        </el-table-column>
        <el-table-column
          label="创建时间">
          <template slot-scope="scope">
            <span>{{scope.row.add_time | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="editUseMessage(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="菜单名称">
            <el-input v-model="form.menu_name" placeholder="请输入角色名称"></el-input>
          </el-form-item>
          <el-form-item label="菜单描述">
            <el-input type="textarea" maxlength="100" v-model="form.desc" placeholder="请输入角色描述"></el-input>
          </el-form-item>
          <el-form-item label="菜单图标">
            <el-input v-model="form.icon" placeholder="请输入角色描述"></el-input>
          </el-form-item>
          <el-form-item label="菜单url">
            <el-input v-model="form.url" placeholder="请输入角色描述"></el-input>
          </el-form-item>
          <el-form-item label="上级菜单">
            <el-select v-model="form.pid" placeholder="请选择上级菜单">
              <el-option label="新增部门" :value="0"></el-option>
              <el-option v-for="item in menuList" :key="item.menu_id" :label="item.menu_name" :value="item.menu_id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                props: {
                    label: 'name',
                    children: 'zones'
                },
                updateMenu: null,
                count: 1,
                editUse: null,
                title: '菜单信息',
                dialogVisible: false,
                menuList: [],
                form: {
                    desc: '',
                    menu_name: '',
                    pid: '',
                    icon: '',
                    url: ''
                }
            }
        },
        methods: {
            getmenuList() {
                this.$post('/getMenuList').then(res => {
                    this.menuList = res;
                })
            },
            editUseMessage(data) {
                this.dialogVisible = true;
                this.updateMenu = data;
                if (data.menu_id) {
                    this.form = data
                } else {
                    this.form.menu_name = '';
                    this.form.desc = '';
                    this.form.pid = '';
                }
            },
            updateStatus(data) {
                this.$post('editMenuStatus', {
                    menu_id: data.menu_id
                }).then(res => {
                    this.getmenuList();
                    this.$message.success('状态修改成功')
                })
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.form.menu_name === '') {
                    this.$message.error('菜单名不能为空');
                    return;
                }
                if (this.form.url === '') {
                    this.$message.error('菜单地址不能为空');
                    return;
                }
                if (this.form.icon === '') {
                    this.$message.error('菜单图标不能为空');
                    return;
                }
                if(this.form.pid === '') {
                    this.$message.error('上级菜单不能为空');
                    return;
                }
                if (this.updateMenu.menu_id) {
                    this.$post('editMenu',{
                        menu_name: this.form.menu_name,
                        pid: this.form.pid,
                        icon: this.form.icon,
                        url: this.form.url,
                        menu_id: this.updateMenu.menu_id,
                        status: this.updateMenu.status
                    }).then(res => {
                        this.$message.success('菜单修改成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getmenuList();
                    })
                } else {
                    this.$post('addMenu',
                        this.form
                    ).then(res => {
                        this.$message.success('菜单添加成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getmenuList()
                    });
                }

            },
            handleCheckChange(data, checked, indeterminate) {
                console.log(data, checked, indeterminate);
            },
            handleNodeClick(data) {
                console.log(data);
            }
        },
        mounted() {
            this.getmenuList()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
</style>



