<template>
  <div style="text-align: right; padding: 14px;">
    <el-pagination
      @size-change="onSizeChange"
      @current-change="onNumChange"
      :current-page="num"
      :page-sizes="[50, 100, 200]"
      :page-size="size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
    export default {
        data() {
            return {
                size: this.pageSize,
                num: this.pageNum
            };
        },
        props: ['pageSize', 'pageNum', 'total'],
        methods: {
            onSizeChange(val) {
                this.size = val;
                this.$emit('update:pageSize', val);
                this.$emit('change');
            },
            onNumChange(val) {
                this.num = val;
                this.$emit('update:pageNum', val);
                this.$emit('change');
            }
        }
    };
</script>



