<template>
  <div>
    <NavTemplent title="用户管理"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-form-item label="姓名">
            <el-input v-model="form.userName" placeholder="请输入姓名"></el-input>
          </el-form-item>
          <el-form-item label="部门">
            <el-select v-model="form.departmentId" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getUserList">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row class="tableTitle">
        <el-col :span="12">
          <h5>用户列表</h5>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <el-button size="mini" type="primary" @click="editUseMessage"><i class="el-icon-plus"></i>添加</el-button>
        </el-col>
      </el-row>
      <el-table
        :data="userList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="用户名称">
          <template slot-scope="scope">
            <span>{{scope.row.name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="部门">
          <template slot-scope="scope">
            <span>{{scope.row.department_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="角色">
          <template slot-scope="scope">
            <span>{{scope.row.role_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="状态">
          <template slot-scope="scope">
            <el-button type="success" size="mini" @click="updateStatus(scope.row)" v-if="scope.row.status === 1">启用</el-button>
            <el-button type="danger" size="mini" @click="updateStatus(scope.row)" v-else>禁用</el-button>
          </template>
        </el-table-column>
        <el-table-column
          label="创建时间">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="editUseMessage(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="姓名" v-if="!uditUser">
            <el-input v-model="form.name" placeholder="请输入部门负责人名称"></el-input>
          </el-form-item>
          <el-form-item label="手机号" v-if="!uditUser">
            <el-input v-model="form.mobile" placeholder="请输入手机号"></el-input>
          </el-form-item>
          <el-form-item label="密码" v-if="!uditUser">
            <el-input type="password" v-model="form.password" placeholder="******"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" v-if="!uditUser">
            <el-input v-model="form.email" placeholder="请输入邮箱"></el-input>
          </el-form-item>
          <el-form-item label="部门">
            <el-select v-model="form.department_id" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="角色" v-if="uditUser" >
            <el-select v-model="form.role_id" placeholder="请选择角色" multiple>
              <el-option v-for="item in roleList" :key="item.role_id" :label="item.role_name" :value="item.role_id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                editUserMessage: null,
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                editUse: null,
                title: '用户信息',
                dialogVisible: false,
                userList: [],
                roleList: [],
                total: 0,
                updateStatusData: null,
                menu: false,
                uditUser: false,
                form: {
                    departmentId: '',
                    password: '',
                    mobile: '',
                    userName: '',
                    department_id: '',
                    email: '',
                    name: '',
                    role_id: [],
                    status: 0
                }
            }
        },
        methods: {
            getUserList() {
                this.$post('/getUserList', {
                    user_name: this.form.userName,
                    department_id: this.form.departmentId
                }).then(res => {
                    this.total = res.count;
                    this.userList = res.list
                })
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            getRoleList() {
                this.$post('getRoleList').then(res => {
                    this.roleList = res.list.filter(item => {
                        return item.status === 1
                    })
                })
            },
            editUseMessage(data) {
                this.editUserMessage = data;
                this.dialogVisible = true;
                if (data.id) {
                    this.form = data;
                    this.menu = true;
                    this.uditUser = false;
                }else {
                    this.form.mobile = '';
                    this.form.email = '';
                    this.form.name = '';
                    this.form.department_id = '';
                    this.form.role_id = [];
                    this.menu = false;
                    this.uditUser = false;
                }
            },
            updateStatus(data) {
                this.editUserMessage = data;
                if (data.status === 1) {
                    this.dialogVisible = true;
                    this.uditUser = true;
                } else {
                    this.$post('editUserStatus', {
                        user_id: data.id
                    }).then(res => {
                        this.getUserList();
                        this.$message.success('状态修改成功')
                    })
                }
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.editUserMessage.id) {
                    if (this.form.mobile === this.editUserMessage.mobile) {
                        this.mobile = null
                    } else {
                        this.mobile = this.form.mobile
                    }
                    if (this.uditUser) {
                        this.$post('editUser', {
                            email: this.editUserMessage.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.editUserMessage.name,
                            mobile: null,
                            department_id: this.form.department_id,
                            password: this.editUserMessage.password,
                            role_list: JSON.stringify(this.form.role_id)
                        }).then(res => {
                            this.$post('editUserStatus', {
                                user_id: this.editUserMessage.id
                            }).then(res => {
                                this.$message.success('状态修改成功');
                                this.dialogVisible = false;
                                this.$emit('close');
                                this.getUserList()
                            });
                        })
                    } else {
                        this.$post('editUser', {
                            email: this.form.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.form.name,
                            mobile: this.mobile,
                            department_id: this.form.department_id,
                            password: this.form.password,
                        }).then(res => {
                            this.$message.success('用户修改成功');
                            this.dialogVisible = false;
                            this.$emit('close');
                            this.getUserList()
                        })
                    }

                }else {
                    console.log(this.form);
                    this.$post('signUp', this.form).then(res => {
                        this.$message.success('用户新增成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getUserList()
                    })
                }
            }
        },
        mounted() {
            this.getUserList();
            this.getDepartmentList();
            this.getRoleList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
</style>



