import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/Auth/Login/index.vue'
import Register from '@/views/Auth/Register/index.vue'
import Common from "../components/Common";
import Dashbord from '@/views/Dashbord/index.vue'
import LogisticsCosts from '@/views/CostStatistics/LogisticsCosts/index.vue'
import PromotionCosts from '@/views/CostStatistics/PromotionCosts/index.vue'
import MaterialCosts from '@/views/CostStatistics/MaterialCosts/index.vue'
import ProcurementCosts from '@/views/CostStatistics/ProcurementCosts/index.vue'
import LogisticsCostsData from '@/views/CostData/LogisticsCosts/index.vue'
import PromotionCostsData from '@/views/CostData/PromotionCosts/index.vue'
import MaterialCostsData from '@/views/CostData/MaterialCosts/index.vue'
import ProcurementCostsData from '@/views/CostData/ProcurementCosts/index.vue'
import TotalData from '@/views/CostData/TotalData/index.vue'
import Custom from '@/views/DataAnalysis/Custom/index.vue'
import Convert from '@/views/DataAnalysis/Convert/index.vue'
import DepartmentHead from '@/views/DepartmentIndicators/DepartmentHead/index.vue'
import PersonnelDepartment from '@/views/DepartmentIndicators/PersonnelDepartment/index.vue'
import ProductDepartment from '@/views/DepartmentIndicators/ProductDepartment/index.vue'
import BusinessDepartment from '@/views/DepartmentIndicators/BusinessDepartment/index.vue'
import OperatingDepartment from '@/views/DepartmentIndicators/OperatingDepartment/index.vue'
import CustomerDepartment from '@/views/DepartmentIndicators/CustomerDepartment/index.vue'
import ProcurementDepartment from '@/views/DepartmentIndicators/ProcurementDepartment/index.vue'
import WarehouseDepartment from '@/views/DepartmentIndicators/WarehouseDepartment/index.vue'
import UseManage from '@/views/SysteManagement/UseManage/index.vue'
import DepartmentManage from '@/views/SysteManagement/DepartmentManage/index.vue'
import MenuManage from '@/views/SysteManagement/MenuManage/index.vue'
import SystemLog from '@/views/SysteManagement/SystemLog/index.vue'
import IndicatorsManage from '@/views/SysteManagement/IndicatorsManage/index.vue'
import SiteManage from '@/views/SysteManagement/SiteManage/index.vue'
import OperationManual from '@/views/SysteManagement/OperationManual/index.vue'
import RoleManage from '@/views/SysteManagement/RoleManage/index.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/Register',
      name: 'Register',
      component: Register
    },
    {
      path: '/Common',
      name: 'Common',
      component: Common,
      meta: {
        requireAuth: true,
      },
      children: [
        {
          path:'/Dashbord',
          name: 'Dashbord',
          component: Dashbord,
        },
        {
          path:'/LogisticsCosts',
          name: 'LogisticsCosts',
          component: LogisticsCosts
        },
        {
          path:'/PromotionCosts',
          name: 'PromotionCosts',
          component: PromotionCosts,
        },
        {
          path:'/ProcurementCosts',
          name: 'ProcurementCosts',
          component: ProcurementCosts,
        },
        {
          path:'/MaterialCosts',
          name: 'MaterialCosts',
          component: MaterialCosts,
        },
        {
          path:'/LogisticsCostsData',
          name: 'LogisticsCostsData',
          component: LogisticsCostsData
        },
        {
          path:'/PromotionCostsData',
          name: 'PromotionCostsData',
          component: PromotionCostsData,
        },
        {
          path:'/ProcurementCostsData',
          name: 'ProcurementCostsData',
          component: ProcurementCostsData,
        },
        {
          path:'/MaterialCostsData',
          name: 'MaterialCostsData',
          component: MaterialCostsData,
        },
        {
          path:'/TotalData',
          name: 'TotalData',
          component: TotalData,
        },
        {
          path:'/Custom',
          name: 'Custom',
          component: Custom,
        },
        {
          path:'/Convert',
          name: 'Convert',
          component: Convert,
        },
        {
          path:'/DepartmentHead',
          name: 'DepartmentHead',
          component: DepartmentHead,
        },
        {
          path:'/PersonnelDepartment',
          name: 'PersonnelDepartment',
          component: PersonnelDepartment,
        },
        {
          path:'/ProductDepartment',
          name: 'ProductDepartment',
          component: ProductDepartment,
        },
        {
          path:'/BusinessDepartment',
          name: 'BusinessDepartment',
          component: BusinessDepartment,
        },
        {
          path:'/OperatingDepartment',
          name: 'OperatingDepartment',
          component: OperatingDepartment,
        },
        {
          path:'/CustomerDepartment',
          name: 'CustomerDepartment',
          component: CustomerDepartment,
        },
        {
          path:'/ProcurementDepartment',
          name: 'ProcurementDepartment',
          component: ProcurementDepartment,
        },
        {
          path:'/WarehouseDepartment',
          name: 'WarehouseDepartment',
          component: WarehouseDepartment,
        },
        {
          path:'/UseManage',
          name: 'UseManage',
          component: UseManage,
        },
        {
          path:'/DepartmentManage',
          name: 'DepartmentManage',
          component: DepartmentManage,
        },
        {
          path:'/MenuManage',
          name: 'MenuManage',
          component: MenuManage,
        },
        {
          path:'/SystemLog',
          name: 'SystemLog',
          component: SystemLog,
        },
        {
          path:'/IndicatorsManage',
          name: 'IndicatorsManage',
          component: IndicatorsManage,
        },
        {
          path:'/SiteManage',
          name: 'SiteManage',
          component: SiteManage,
        },
        {
          path:'/OperationManual',
          name: 'OperationManual',
          component: OperationManual,
        },
        {
          path:'/RoleManage',
          name: 'RoleManage',
          component: RoleManage,
        },
      ]
    }
  ]
})



