<template>
  <div>
    <NavTemplent title="采购费用"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="费用类型">
            <el-select v-model="form.departmentId" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="站点">
            <el-select v-model="form.site" placeholder="请选择站点">
              <el-option v-for="item in siteList" :key="item.website_id" :value="item.website_id" :label="item.website_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getUserList">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row class="tableTitle">
        <el-col :span="24">
          <h5>采购费用</h5>
        </el-col>
      </el-row>
      <div>
        <div id="myChart" style="width: 100%;height: 300px"></div>
      </div>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="姓名">
            <el-input v-model="form.name" placeholder="请输入部门负责人名称"></el-input>
          </el-form-item>
          <el-form-item label="广告费用">
            <el-input v-model="form.mobile" placeholder="请输入手机号"></el-input>
          </el-form-item>
          <el-form-item label="销售额">
            <el-input type="password" v-model="form.password" placeholder="******"></el-input>
          </el-form-item>
          <el-form-item label="ROI">
            <el-input v-model="form.email" placeholder="请输入邮箱"></el-input>
          </el-form-item>
          <el-form-item label="渠道">
            <el-select v-model="form.department_id" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="分析原因">
            <el-select v-model="form.role_id" placeholder="请选择角色" multiple>
              <el-option v-for="item in roleList" :key="item.role_id" :label="item.role_name" :value="item.role_id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                siteList: null,
                editUserMessage: null,
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                editUse: null,
                dialogVisible: false,
                promotionList: [],
                roleList: [],
                total: 0,
                updateStatusData: null,
                menu: false,
                title: '编辑费用',
                form: {
                    date: [],
                    departmentId: '',
                    password: '',
                    mobile: '',
                    userName: '',
                    department_id: '',
                    email: '',
                    name: '',
                    role_id: [],
                    status: 0
                }
            }
        },
        methods: {
            getSiteList() {
                this.$post('/getWebsiteList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            drawLine() {
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                myChart.setOption({
                    xAxis: {
                        type: 'category',
                        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [20, 32, 91, 94, 290, 330, 320],
                        type: 'line'
                    }]
                });
            },
            getUserList() {
                this.$post('/getUserList', {
                    user_name: this.form.userName,
                    department_id: this.form.departmentId
                }).then(res => {
                    this.total = res.count;
                    this.userList = res.list
                })
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            getRoleList() {
                this.$post('getRoleList').then(res => {
                    this.roleList = res.list.filter(item => {
                        return item.status === 1
                    })
                })
            },
            editUseMessage(data) {
                this.editUserMessage = data;
                this.dialogVisible = true;
                if (data.id) {
                    this.form = data;
                    this.menu = true;
                }else {
                    this.form.mobile = '';
                    this.form.email = '';
                    this.form.name = '';
                    this.form.department_id = '';
                    this.form.role_id = [];
                    this.menu = false;
                }
            },
            updateStatus(data) {
                this.editUserMessage = data;
                if (data.status === 1) {
                    this.dialogVisible = true;
                } else {
                    this.$post('editUserStatus', {
                        user_id: data.id
                    }).then(res => {
                        this.getUserList();
                        this.$message.success('状态修改成功')
                    })
                }
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.editUserMessage.id) {
                    if (this.form.mobile === this.editUserMessage.mobile) {
                        this.mobile = null
                    } else {
                        this.mobile = this.form.mobile
                    }
                    if (this.uditUser) {
                        this.$post('editUser', {
                            email: this.editUserMessage.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.editUserMessage.name,
                            mobile: null,
                            department_id: this.form.department_id,
                            password: this.editUserMessage.password,
                            role_list: JSON.stringify(this.form.role_id)
                        }).then(res => {
                            this.$post('editUserStatus', {
                                user_id: this.editUserMessage.id
                            }).then(res => {
                                this.$message.success('状态修改成功');
                                this.dialogVisible = false;
                                this.$emit('close');
                                this.getUserList()
                            });
                        })
                    } else {
                        this.$post('editUser', {
                            email: this.form.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.form.name,
                            mobile: this.mobile,
                            department_id: this.form.department_id,
                            password: this.form.password,
                        }).then(res => {
                            this.$message.success('用户修改成功');
                            this.dialogVisible = false;
                            this.$emit('close');
                            this.getUserList()
                        })
                    }

                }else {
                    console.log(this.form);
                    this.$post('signUp', this.form).then(res => {
                        this.$message.success('用户新增成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getUserList()
                    })
                }
            }
        },
        mounted() {
            this.getSiteList();
            this.getUserList();
            this.getDepartmentList();
            this.getRoleList();
            this.drawLine()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



