<template>
  <div>
    <NavTemplent title="系统日志"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            @change="getSystemLogList"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="姓名">
            <el-input v-model="form.userName" placeholder="请输入姓名" @change="getSystemLogList"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row class="tableTitle">
        <el-col :span="24">
          <h5>日志列表</h5>
        </el-col>
      </el-row>
      <el-table
        :data="systemLogList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="用户名">
          <template slot-scope="scope">
            <span>{{scope.row.user_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="备注">
          <template slot-scope="scope">
            <span>{{scope.row.note || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="IP地址">
          <template slot-scope="scope">
            <span>{{scope.row.ip || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="创建时间">
          <template slot-scope="scope">
            {{scope.row.add_time | dateFormat('YYYY/MM/DD')}}
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"></TablePage>
    </div>
  </div>
</template>
<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                total: 0,
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                systemLogList: [],
                form: {
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    userName: ''
                }
            }
        },
        methods: {
            getSystemLogList() {
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$post('/getSystemLog', {
                    page: this.page.pageSize,
                    pageNum: this.page.pageNum,
                    start_time: this.startTime,
                    end_time: this.lastTime,
                    user_name: this.form.userName
                }).then(res => {
                    this.total = res.count;
                    this.systemLogList = res.list
                })
            }
        },
        mounted() {
            this.getSystemLogList()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
</style>



