<template>
    <el-row class="navTemplate">
      <el-col :span="12">
        <span style="text-align: left">&nbsp;&nbsp;{{title}}</span>
      </el-col>
      <el-col :span="12">
        <div style="text-align: right;padding-right: 20%">
          <el-button><i class="el-icon-refresh"></i>刷新</el-button>
          <el-button><i class="el-icon-arrow-left"></i>返回</el-button>
        </div>
      </el-col>
    </el-row>
</template>

<script>
    export default {
        props: ['title']
    }
</script>

<style lang="scss" scoped>
  .navTemplate{
    height: 54px;
    line-height: 54px;
    padding: 0 15px;
    border-bottom: 1px solid #e2dbdb;

    .el-button{
      width: 70px;
      padding: 7px 10px;
    }
    span{
      font-weight: bold;
      border-left: 5px solid #409EFF;
    }
  }
</style>



