<template>
    <div style="line-height: 54px">
      <span @click="loginOut">退出登录</span>
    </div>
</template>

<script>
    export default {
        methods: {
            loginOut() {
                this.$post('/logout').then(res => {
                    this.$router.push({
                        path: '/'
                    });
                    localStorage.clear();
                })
            }
        }
    }
</script>

<style scoped>

</style>



