<template>
  <div>
    <NavTemplent title="指标管理"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-form-item label="部门">
            <el-select v-model="form.departmentId" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getindicatorsList">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row class="tableTitle">
        <el-col :span="12">
          <h5>指标列表</h5>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <el-button size="mini" type="primary" @click="editUseMessage"><i class="el-icon-plus"></i>添加</el-button>
        </el-col>
      </el-row>
      <el-table
        :data="indicatorsList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="部门名称">
          <template slot-scope="scope">
            <span>{{scope.row.department_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="指标名称">
          <template slot-scope="scope">
            <span>{{scope.row.quota_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="更新时间">
          <template slot-scope="scope">
            <span>{{scope.row.add_time | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="editUseMessage(scope.row)">编辑</el-button>
            <el-button type="danger" size="small" @click="delUseMessage(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="部门">
            <el-select v-model="form.department_id" placeholder="请选择部门">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="指标名称">
            <el-input v-model="form.quota_name" placeholder="请输入指标名称"></el-input>
          </el-form-item>
          <el-form-item label="指标描述">
            <el-input type="textarea" maxlength="100" show-word-limit v-model="form.quota_desc" placeholder="请输入指标描述"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                editUse: null,
                title: '指标信息',
                dialogVisible: false,
                indicatorsList: [],
                departmentList: [],
                quotaMessage: null,
                total: 0,
                form: {
                    department_id: '',
                    departmentId: '',
                    quota_name: '',
                    quota_desc: ''
                }
            }
        },
        methods: {
            getindicatorsList() {
                this.$post('/getQuotaList', {
                    department_id: this.form.departmentId,
                    page: this.page.pageSize,
                    pageNum: this.page.pageNum
                }).then(res => {
                    this.indicatorsList = res.list;
                    this.total = res.count;
                })
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            editUseMessage(data) {
                this.dialogVisible = true;
                this.quotaMessage = data;
                if (data.quota_id) {
                    this.form = data;
                } else {
                    this.form.quota_name = '';
                    this.form.quota_desc = '';
                    this.form.department_id = '';
                }
            },
            delUseMessage(data) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$post('delQuota', {
                        quota_id: data.quota_id
                    }).then(() => {
                        this.getindicatorsList();
                        this.$message.success('指标删除成功')
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.form.department_id === '') {
                    this.$message.error('部门不能为空');
                    return;
                }
                if (this.form.quota_name === '') {
                    this.$message.error('指标名不能为空');
                    return;
                }
                if (this.quotaMessage.quota_id) {
                    this.form.quota_id = this.quotaMessage.quota_id;
                    this.$post('editQuota', this.form).then(() => {
                        this.$message.success('指标新增成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getindicatorsList();
                    })
                } else {
                    this.$post('addQuota', this.form).then(() => {
                        this.$message.success('指标新增成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getindicatorsList();
                    });
                }

            }
        },
        mounted() {
            this.getDepartmentList();
            this.getindicatorsList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
</style>



